<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container">
      <div class="login">
        <div class="row">
          <div class="col-xl-5">
            <div class="log_img">
              <div class="log_img_title">Kirish</div>
              <img src="../../../assets/images/llaptop.png" alt="">
            </div>
          </div>
          <div class="col-xl-7">
            <div class="log_form">
             <div>
               <form @submit.prevent="login()">
                 <label>Email *</label>
                 <div class="input_text">
                   <input type="email" v-model="email" placeholder="Email">
                 </div>
                 <label>Parol*</label>
                 <div class="input_text">
                   <input type="password" v-model="password" placeholder="Parol">
                 </div>
                 <div class="forgot_password">
                   <router-link to="/" class="decoration">
                    Parolni tiklash
                   </router-link>
                 </div>
                 <div class="log_btn">
                   <button> Kirish</button>
                 </div>
                 <div class="phone_link">
                   <router-link to="/phone_reg" >
                     <div>
                       Telefon raqam orqali kirish
                     </div>
                   </router-link>
                 </div>
               </form>
               <div class="reg_links">
                 <router-link to="/registration">
                   <div>Ro'yxatdan o'tmaganmisz? <span>Ro'yxatdan o'tish</span></div>
                 </router-link>
               </div>
             </div>
            </div>
          </div>
        </div>

      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";
export default {
  components: {Footer, Header},
  data(){
    return{
      email:'',
      password:''
    }
  },
  methods:{
    login(){
      this.$axiosDefault.post('/students/login/',{
        "email":this.email,
        "password":this.password
      })
          .then(res=>{
            localStorage.setItem('reg_token',res.data.access)
            this.$store.dispatch('getMe')
            this.$router.push('/')

            })
          .catch(()=>{
            this.$toast("Email yoki parol xato ", {
              timeout: 2000,
              type:"error"
            });
          })

    }
  }
}
</script>
